.center {
  flex-flow: column wrap;
  align-items: center;
  display: flex;
}

.controls {
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  display: flex;
}

.buttons {
  gap: 5px;
  display: flex;
}

.timer {
  gap: 20px;
  margin: 30px 0;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-size: 30px;
}

.label {
  text-transform: uppercase;
  font-weight: 500;
}

.border {
  border: 2px solid #a34d4d;
  border-radius: 10px;
  width: 200px;
  height: 50px;
  padding: 20px;
}

.change {
  border-color: #1b7ee0;
}

.correct {
  border-color: #228b22;
}

#datetime-picker:hover {
  border-color: #1b7ee0;
}

#datetime-picker:focus {
  outline-color: #1b7ee0;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #00bd00;
  border: 2px solid #228b22;
  border-radius: 10px;
  padding: 16px 30px;
  font-weight: 700;
}

button:hover {
  background-color: #4cdd4c;
}

button:disabled {
  color: gray;
  cursor: no-drop;
  background-color: #d3d3d3;
  border-color: gray;
}

button:last-of-type {
  color: #fff;
  background-color: #ee5353;
  border-color: #a34d4d;
}

button:last-of-type:hover {
  background-color: #f08080;
}

button:last-of-type:disabled {
  color: gray;
  cursor: no-drop;
  background-color: #d3d3d3;
  border-color: gray;
}

.pedro {
  border-radius: 50%;
}

.hidden {
  display: none;
}
